@font-face {
  font-family: BrandonGrotesque;
  src: url('../../Library/Fonts/Brandon_reg.otf');
}

@font-face {
  font-family: BrandonGrotesqueBold;
  src: url('../../Library/Fonts/Brandon_bld.otf');
}

@font-face {
  font-family: EBGaramond;
  src: url('../../Library/Fonts/EBGaramond_reg.ttf');
}

@font-face {
  font-family: EBGaramondSemiBold;
  src: url('../../Library/Fonts/EBGaramond_semibld.ttf');
}

html, body, #root {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: BrandonGrotesque;
  background-color: white;
}

::-webkit-input-placeholder {
   color: #d9d9d9;
}

:-moz-placeholder { /* Firefox 18- */
   color: #d9d9d9; 
   opacity:  1;
}

::-moz-placeholder {  /* Firefox 19+ */
   color: #d9d9d9;  
   opacity:  1;
}

:-ms-input-placeholder {  
   color: #d9d9d9;  
}

.enter {
  fill: #34b269;
}

.update {
  fill: #333;
}

.exit {
  fill: #eb5757;
}

.zopim {
  z-index: 100 !important;
}



